import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { BaseInterest } from "./base-interest.component";
import { BaseInterestAddComponent } from "./base-interest-add/base-interest-add.component";
import { BaseInterestImportComponent } from "./base-interest-import/base-interest-import.component";
// import { QuerySubpageComponent } from "./query-subpage/query-subpage.component";

export const routes: Routes = [
    {
        path: '',component: BaseInterest,
    },  
    {path:'base-interest-add',component:BaseInterestAddComponent},
    {path:'base-interest-import',component:BaseInterestImportComponent},
    // {path:'query-subpage',component:QuerySubpageComponent}
];



export const BaseInterestRouting: ModuleWithProviders = RouterModule.forChild(routes);
