import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { SelectItem } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng'
import { BeanInterest } from './../bean/base-interest.bean';
import { Message } from 'primeng/primeng';
// import { IsUnit, IsLable2, IsOrigin } from './../../constant/codeValue';
import { TZB_HTTP_VALUpload } from 'app/@uisftech/common/service/http.constant';
import { TreeNode } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { blank } from 'app/@uisftech/common/service/validator'
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
@Component({
  selector: 'app-base-interest-import',
  templateUrl: './base-interest-import.component.html',
  styleUrls: ['./base-interest-import.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class BaseInterestImportComponent implements OnInit {
  //传入的值
  @Input() inValue: string;
  //出参
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  uploadFlag:boolean = true;
  //提示信息
  msgs: Message[] = [];
  date;
  beanInterest: BeanInterest = new BeanInterest();
  blank = blank;
  ch: any = CALENDAR_CN;
  cities: SelectItem[];
  selectedCity: string;
  selectedCities: string[];
  isUnit: any = [];
  isOrigin: any = [];
  isLable2: any = [];
  UPLOAD: string = TZB_HTTP_VALUpload;
  files: any = [];
  fileList: any = [];//上传文件列表
  fileUrl: string;
  fileName: string;
  fileNameLen: number;
  filesTree: TreeNode[];
  selectedFiles: TreeNode[];
  treeList2: string;
  treeSelect2: any;
  userform: FormGroup; //校验 
  effectiveDate: Date;
  expirationDate: Date;
  list1: any = [];
  taskCategoryId: string;
  button: boolean = false;
  mechanismShow: boolean = false;
  //机构数模态框
  orgTreeDisplay: boolean = false;
  orgTreeNameList: string = '';//接受选中的机构树name
  isDisable: boolean = false;
  data: any = {};
  code: any;//获取所有码值
  showTree: boolean = true;
  constructor(public httpService: ValuationHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc, public fb: FormBuilder, public router: Router,
    public activatedRoute: ActivatedRoute, ) {
    this.codeValues();
    this.isOrigin = this.code['IsOrigin'];
    this.isLable2 = this.code['IsLable2'];
    this.isUnit = this.code['IsUnit'];
    this.cities = [];
    this.cities.push({ label: '请选择', value: 'New York' });

    //审核，工作流
    if (this.activatedRoute.snapshot.params.taskCategoryId) {
      this.taskCategoryId = this.activatedRoute.snapshot.params.taskCategoryId;
    }
  }

  ngOnInit() {
    let session = window.sessionStorage;
    if (session.getItem('para')) {
      session.removeItem('para');
    }
    this.filesTree = [
      {
        "label": "99-总行",
        "data": "99-总行",
        "expandedIcon": "fa-folder-open",
        "collapsedIcon": "fa-folder",
        "children": [
          { "label": "9901-机构片区A", "icon": "fa-file-image-o", "data": "Barcelona Photo" },
          { "label": "9902-机构片区B", "icon": "fa-file-image-o", "data": "PrimeFaces Logo" },
          { "label": "9903-机构片区C", "icon": "fa-file-image-o", "data": "PrimeUI Logo" }]
      },
    ];
    this.treeList2 = JSON.stringify(this.filesTree);
    //利率种类编码调的服务
    this.httpService.queryRateTypeInternalListService(this.beanInterest).subscribe(data => {
      // this.beanInterest = data.resultList;
      if (data.resultList) {
        this.list1 = this.commfunc.toChangeTree(data.resultList, "intRateName", "intRateCgy")
        this.list1.unshift({ label: '请选择', value: '' });

      }
      // data.resultList.forEach((item) => {
      //   let obj = {};
      //   obj['label'] = item.intRateName;
      //   obj['value'] = item;
      //   this.list1.push(obj)
      // })
    });

    this.userform = this.fb.group({
      'effectiveDate': new FormControl('', Validators.required),
      'expirationDate': new FormControl(''),
      'fileName': new FormControl('', [Validators.required, blank]),
      'interestUnit': new FormControl('', Validators.required),
      'intRateCgy': new FormControl('', Validators.required),
      'intRateFlgCode': new FormControl(''),
      'intRateSrcCode': new FormControl(''),
      'mechanismList': new FormControl(''),
    });
  }
  treeClick(param) {
    // alert(JSON.stringify(param.data));
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //提交
  handOn() {
    
    if (!this.uploadFlag){
      // alert("文件未上传");
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "文件未上传!" });
      return
    }
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }

    // 导入文件名称长度验证
    if (this.fileNameLen > 84) {
      return
    }
    //机构树必输
    if (this.beanInterest.intRateSrcCode == '2' && !this.orgTreeNameList) {
      return
    }

    //处理工作流的参数
    this.beanInterest['taskCategoryId'] = this.taskCategoryId + '';
    this.beanInterest.fileName = this.fileList[0].fileName;
    this.beanInterest.effectiveDate = this.commfunc.transDateN(this.effectiveDate);//生效日期
    this.beanInterest.expirationDate = this.commfunc.transDateN(this.expirationDate);//失效日期
    this.isDisable = true;
    this.httpService.importLoansBaseRateService(this.beanInterest).subscribe(data => {

      if (data.returnCode.code == "success") {
        // this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        setTimeout(() => {
          window.history.go(-1);
        }, 1000)
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        this.isDisable = false;
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
      this.isDisable = false;
    })
  }
  //取消
  cancel() {
    window.history.go(-1);
  }
  onSelect(event) {
    
    this.uploadFlag = false;
  }
  //上传文件，浏览
  load(event) {
    this.uploadFlag = true;
    for (let file of event.files) {
      this.files.push(file)
    }
    if (event.xhr.response) { //上传成功 后台返回的 文件路径 和文件名
      this.fileList = JSON.parse(event.xhr.response);
    }
    let param = { fileList: this.fileList };
    this.beanInterest.fileName = this.fileList[0].fileName;
    this.fileNameLen = this.beanInterest.fileName.length
    // let param = { fileList: this.fileList };
    // this.httpService.importLoansBaseRateService(param).subscribe(data => {
    //   this.beanInterest.fileName = this.fileList[0].fileName;
    // })
  }

  /**
	 * 机构树模态框开始
	 */
  chooseTree() {
    this.orgTreeDisplay = !this.orgTreeDisplay;
    if (!this.orgTreeNameList) {
      this.showTree = !this.showTree;
    }
  }
  // 机构树回调
  treeCall(param) {
    this.orgTreeDisplay = false;
    this.orgTreeNameList = '';
    this.beanInterest.mechanismList = [];
    // this.beanInterest.locationList = [];
    param.forEach(item => {
      this.beanInterest.mechanismList.push(item.orgId);
      // this.beanInterest.locationList.push(item.location);
      this.orgTreeNameList += item.orgName + ",";
      // if(!this.beanInterest.mechanismList){
      //   item.orgId="" ;
      //   item.orgName=""
      // }
    })

  }
	/**
	 * 机构树模态框结束
	 */
  doChose() {

    let ob = this.httpService.queryRateTypeInternalListService(this.beanInterest)
    ob.subscribe(data => {
      if (data.resultList) {
        this.data = data.resultList;
      }
      //给利率标识赋值
      if (this.beanInterest['intRateCgy'] == this.data[0]['intRateCgy']) {
        this.beanInterest.intRateFlgCode = this.data[0]['intRateFlgCode']
      }
      //给利率来源赋值
      if (this.beanInterest['intRateCgy'] == this.data[0]['intRateCgy']) {
        this.beanInterest.intRateSrcCode = this.data[0]['intRateSrcCode']
        if (this.beanInterest['intRateSrcCode'] != "1") {
          this.mechanismShow = false;
        } else {
          this.orgTreeNameList = "";
          this.beanInterest['mechanismList'] = [];
          this.mechanismShow = true;
        }
      }
    })
  }
}