import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { BeanInterest } from './../bean/base-interest.bean'
import { ConfirmationService } from 'primeng/primeng';
// import { IsValue, IsUnit, IsLable2, IsOrigin } from './../../constant/codeValue';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { TwoInteger } from 'app/@uisftech/common/service/validator';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";

@Component({
  selector: 'app-base-interest-add',
  templateUrl: './base-interest-add.component.html',
  styleUrls: ['./base-interest-add.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class BaseInterestAddComponent implements OnInit, OnChanges {
  //传入的值
  @Input() inValue: string;
  //出参
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  //bean
  beanInterest: BeanInterest = new BeanInterest();
  date3;
  ch: any = CALENDAR_CN;
  show1: boolean = false;
  isRed1: boolean = false;
  isRed2: boolean = false;
  isRed3: boolean = false;
  list: any = [];
  list1: any = [];
  list2: any = [];
  list3: any = [];
  list4: any = [];
  list6: any = [];
  //提示信息
  msgs: Message[] = [];
  data: any = {};
  //模态框
  headerTitle: string;//标题
  display: boolean = false;//是否展现
  showModel: string;//展现哪一个
  //机构数模态框
  treeOrgName: string = '';
  //传入详情的值
  inValueCode: string;
  totalNum: string;
  isValue: any = [];
  isUnit: any = [];
  isLable2: any = [];
  isOrigin: any = [];
  arrList: any = []; //利率种类码值
  userform: FormGroup; //校验 
  effDt: Date;
  invalDt: Date;
  taskCategoryId: string;
  isDisable: boolean = false;
  showTree: boolean = true;
  dmslValDesc: any;
  //获取所有码值
  code: any;
  constructor(
    public httpService: ValuationHttpService,
    public confirmationService: ConfirmationService,
    public router: Router,
    public activatedRoute: ActivatedRoute,
    public commfunc: Commfunc,
    public fb: FormBuilder
  ) {
    this.codeValues();
    this.isOrigin = this.code['IsOrigin'];
    this.isLable2 = this.code['IsLable2'];
    this.isValue = this.code['IsValue'];
    this.isUnit = this.code['IsUnit'];
    //审核，工作流
    if (this.activatedRoute.snapshot.params.taskCategoryId) {
      this.taskCategoryId = this.activatedRoute.snapshot.params.taskCategoryId;
    }
  }
  ngOnInit() {
    let session = window.sessionStorage;
    if (session.getItem('para')) {
      session.removeItem('para');
    }
    // 利率种类名称
    this.httpService.queryRateTypeInternalListService(this.beanInterest).subscribe(data => {
      if (data.resultList) {
        this.list = data.resultList;
        this.arrList = this.commfunc.toChangeTree(data.resultList, "intRateName", "intRateCgy");
      }
    });
    //金额服务 
    let param1 = { dmsnlChiNm: "贷款金额" };
    this.httpService.querydetailTmsDicService(param1).subscribe(data => {
      if (data.resultList) {
        this.list1 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue");
        this.list1.unshift({ label: '请选择', value: '' })
      }
    });
    //担保方式
    let param2 = { dmsnlChiNm: "担保方式" };
    this.httpService.querydetailTmsDicService(param2).subscribe(data => {
      if (data.resultList) {
        this.list2 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue");
        this.list2.unshift({ label: '请选择', value: '' })
      }
    });
    //期限
    let param3 = { dmsnlChiNm: "期限" };
    this.httpService.querydetailTmsDicService(param3).subscribe(data => {
      if (data.resultList) {
        this.list3 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue");
      }
    });
    //积数比
    let param4 = { dmsnlChiNm: "积数比" };
    this.httpService.querydetailTmsDicService(param4).subscribe(data => {
      if (data.resultList) {
        this.list4 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue")
        this.list4.unshift({ label: '请选择', value: '' })
      }
    });
    let param5 = { dmsnlChiNm: "币种" };
    this.httpService.querydetailTmsDicService(param5).subscribe(data => {
      if (data.resultList) {
        this.list6 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue")
      }
    });

    this.userform = this.fb.group({
      // 'intRateName': new FormControl('', Validators.required),
      'intRateNameDisplay': new FormControl('', Validators.required),
      'intRateCgy': new FormControl('', Validators.required),
      'effDt': new FormControl('', Validators.required),
      'ccyCode': new FormControl('', Validators.required),
      'invalDt': new FormControl(''),
      'intClcnAmt': new FormControl(''),
      'intCnt': new FormControl(''),
      'grantMethod': new FormControl(''),
      'intRateMat': new FormControl('', Validators.required),
      'numRatio': new FormControl(''),
      'institArea': new FormControl(''),
      'intRateVal': new FormControl('', [Validators.required, TwoInteger]),
      'intRateUnitTpCode': new FormControl('', Validators.required),
      'intRateFlgCode': new FormControl(''),
      'intRateSrcCode': new FormControl(''),
      'txInf': new FormControl('', Validators.maxLength(42)),
    });
  }
  ngOnChanges() {
    this.beanInterest = new BeanInterest();
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  /**
 * 机构树模态框开始
 */
  chooseTree() {
    this.headerTitle = '选择机构树';
    this.showModel = '2';
    this.display = !this.display;
    if (!this.treeOrgName) {
      this.showTree = !this.showTree;
    }
  }
  // 机构树回调
  treeCall(param) {
    this.display = false;
    this.treeOrgName = '';
    this.beanInterest.institArea = [];
    param.forEach(item => {
      this.beanInterest.institArea.push(item.orgId);
      this.treeOrgName += item.orgName + ",";
    })
  }
	/**
	 * 机构树模态框结束
	 */
  //保存
  add() {
    if (this.isDisable) {
      return;
    }
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }

    //贷款金额非空判断
    if (this.beanInterest.intRateSrcCode == '2' && !this.beanInterest.intClcnAmt) {
      return;
    }
    //积数比非空判断
    if (this.beanInterest.intRateSrcCode == '2' && !this.beanInterest.numRatio) {
      return;
    }
    //担保方式非空判断
    if (this.beanInterest.intRateSrcCode == '2' && !this.beanInterest.grantMethod) {
      return;
    }
    // 机构片区校验
    if (this.beanInterest.intRateSrcCode == '2' && this.treeOrgName == '') {
      return
    }
    // 中文名称转换
    this.arrList.forEach((item) => {
      if (item.value == this.beanInterest.intRateNameDisplay) {
        this.beanInterest.intRateName = item.label
      }
    });
    // this.beanInterest.intRateName = this.beanInterest.intRateNameDisplay['intRateName'] ? this.beanInterest.intRateNameDisplay['intRateName'] : this.beanInterest.intRateNameDisplay;
    //时间戳转为十位

    let aa = this.commfunc.transDateN(this.effDt);//生效日期
    let bb = this.commfunc.transDateN(this.invalDt);//失效日期
    this.beanInterest.effDt = aa;
    this.beanInterest.invalDt = bb;
    //处理工作流的参数
    this.beanInterest['taskCategoryId'] = this.taskCategoryId + '';
    this.isDisable = true;
    //遍历数组
    // for (let i in this.arrList) {
    //   this.beanInterest.intRateName = this.arrList[i].label
    // }
    this.httpService.addLoansBaseRateService(this.beanInterest).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        setTimeout(() => {
          window.history.go(-1);
        }, 500)
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
        this.isDisable = false;
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
      this.isDisable = false;
    })
  }
  //取消
  cancel() {
    window.history.go(-1);
  }
  //利率种类名称代码
  doChose() {
    this.beanInterest.intRateCgy = this.beanInterest.intRateNameDisplay;
    let param = { intRateCgy: this.beanInterest.intRateCgy }
    this.httpService.queryRateTypeInternalListService(param).subscribe(data => {
      if (data.resultList) {
        this.data = data.resultList;
        //给利率标识赋值 给利率来源赋值
        if (this.beanInterest['intRateCgy'] == this.data[0]['intRateCgy']) {
          this.beanInterest.intRateFlgCode = this.data[0]['intRateFlgCode'];
          this.beanInterest.intRateSrcCode = this.data[0]['intRateSrcCode'];
        }
      }
      //利率来源的相关控制
      if (this.beanInterest.intRateSrcCode == '1') {
        this.show1 = true;
        this.beanInterest.intClcnAmt = '';
        this.beanInterest.grantMethod = '';
        this.beanInterest.numRatio = '';
        this.treeOrgName = '';
        this.beanInterest.institArea = [];
      } else {
        this.show1 = false;
      }
    })
  }

  codeValue(val, arr: any = []) {
    let temp = "";
    if (arr.length > 0) {
      arr.forEach(item => {
        if (item['value'] == val) {
          temp = item['label']
        }
      });
    }
    return temp
  }
  //利率来源
  intRateFlgCodeChanges() {

  }


}

