  code: any;
  name: any;
  money: any;
  icon: any;
  method: any;
  day: any;
  numRat: any;
  userform: FormGroup; //校验 
  isDisable: boolean = false;
  data: any = {};

  ngOnInit() {
    this.userform = this.fb.group({
      'intRateCode': new FormControl(''),
      'intRateName': new FormControl('', Validators.required),
      'intRateCgy': new FormControl('', Validators.required),
      'effDt': new FormControl('', Validators.required),
      'ccyCode': new FormControl('', Validators.required),
      'invalDt': new FormControl(''),
      'intClcnAmt': new FormControl(''),
      'intCnt': new FormControl(''),
      'grantMethod': new FormControl('', Validators.required),
      'intRateMat': new FormControl('', Validators.required),
      'numRatio': new FormControl(''),
      'institArea': new FormControl(''),
      'intRateVal': new FormControl('', [Validators.required, TwoInteger]),
      'intRateUnitTpCode': new FormControl('', Validators.required),
      'intRateFlgCode': new FormControl('', Validators.required),
      'intRateSrcCode': new FormControl('', Validators.required),
      'txInf': new FormControl('', Validators.maxLength(42)),
    });

    //查询方法
    this.httpService.queryLoansBaseRateByIdService(this.inValue).subscribe(data => {
      if (data.prpIntBaseRate) {
        this.list = data.prpIntBaseRate;
      }
      if (this.list.intRateSrcCode == '1') {
        this.show1 = true;
        this.icon = '';
        this.method = '';
        this.numRat = '';
        this.orgName = ''
      } else {
        this.show1 = false;
      }
      this.name = this.inValue['intRateCgy'];
      this.code = this.inValue['intRateCgy'];
      this.money = this.inValue['ccyCode'];
      this.icon = this.inValue['intClcnAmt'];
      this.method = this.inValue['grantMethod'];
      this.day = this.inValue['intRateMat'];
      this.numRat = this.inValue['numRatio'];
      this.effDt = new Date(data.prpIntBaseRate['effDt'])
      this.invalDt = new Date(data.prpIntBaseRate['invalDt'])
      this.treeOrgName = this.inValue['institArea'];
    });

  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  ngOnChanges() {
    if (this.inValue.length <= 0) {
      return;
    }
    let param = { intRateName: "" }
    this.httpService.queryRateTypeInternalListService(param).subscribe(data => {
      if (data.returnCode.code == "success") {
        if (data.resultList) {
          this.arrList = this.commfunc.toChangeTree(data.resultList, "intRateName", "intRateCgy")
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
    });

    //金额服务dmsnlValue:this.inValue['intClcnAmt'],
    let param1 = { dmsnlChiNm: "贷款金额" };
    this.httpService.querydetailTmsDicService(param1).subscribe(data => {
      if (data.resultList) {
        this.list1 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue");
        this.list1.unshift({ label: '请选择', value: '' })
      }
    });
    //担保方式
    let param2 = { dmsnlChiNm: "担保方式" };
    this.httpService.querydetailTmsDicService(param2).subscribe(data => {

      if (data.resultList) {
        this.list2 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue");
        this.list2.unshift({ label: '请选择', value: '' })
      }
    });
    //期限
    let param3 = { dmsnlChiNm: "期限" };
    this.httpService.querydetailTmsDicService(param3).subscribe(data => {
      if (data.resultList) {
        this.list3 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue");
        // this.list.intRateMat=this.list3['dmslValDesc']
      }
    });
    //积数比
    let param4 = { dmsnlChiNm: "积数比" };
    this.httpService.querydetailTmsDicService(param4).subscribe(data => {
      if (data.resultList) {
        this.list4 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue");
        this.list4.unshift({ label: '请选择', value: '' })
      }
    });
    let param6 = { dmsnlChiNm: "币种" };
    this.httpService.querydetailTmsDicService(param6).subscribe(data => {
      if (data.resultList) {
        this.list6 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue")
      }
    });
    this.old = this.inValue['effDt'];
    let param5 = { effDt: this.inValue };

    this.queryTree();
  }
  //机构数服务
  queryTree() {
    let params = {
      orgId: this.inValue['institArea'],
      relationshipType: "0"
    }
    this.commonHttpService.queryOrgByOrgIdAndRelationship(params).subscribe(data => {
      this.treeOrgName = this.commfunc.toChangeTree(data.orgList, "orgName", "orgId")
      if (this.treeOrgName.length > 0) {
        this.orgName = this.treeOrgName[0].label;
        this.orgId = this.treeOrgName[0].value;
      }

    })
  }
