  //     }
  //   }, error => {
  //     this.msgs = [];
  //     this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
  //   });
  // }
  //公共机构树点击事件
  chooseTree(param) {
    this.headerTitle = '选择机构树';
    this.showModel = '2';
    this.display = true;
  }

  //公共机构树回掉
  treeCall(param) {
    this.display = false;
    this.treeOrgName = param.orgId;
    let params = {
      orgId: param.orgId,
      relationshipType: "0"
    }
    this.commonHttpService.queryOrgByOrgIdAndRelationship(params).subscribe(data => {
      this.treeOrgName = this.commfunc.toChangeTree(data.orgList, "orgName", "orgId")
      this.orgName = this.treeOrgName[0].label;
      this.orgId = this.treeOrgName[0].value;
    })
  }
  //s鼠标事件，展示全部内容
  // mouseEnter(p, el) {
  //   let clientH = document.body.offsetHeight; //body高度
  //   let clientW = document.body.offsetWidth; //body宽度
  //   let docH = document.body.scrollHeight; //浏览器高度
