import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { BeanInterest } from './bean/base-interest.bean';
import { ConfirmationService } from 'primeng/primeng';
// import { IsRcord, IsUnit, IsOrigin, IsLable2 } from './../constant/codeValue';
import { Message } from 'primeng/primeng';
import { Router } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
import { CommonHttpService } from "app/pages/tzb/common/http/common.http.service";

@Component({
  selector: 'app-base-interest',
  templateUrl: './base-interest.component.html',
  styleUrls: ['./base-interest.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class BaseInterest implements OnInit {
  beanInterest: BeanInterest = new BeanInterest();
  ch: any = CALENDAR_CN;
  inVal: string = 'T0000';
  orgName: string;
  orgId: string;
  //table数据
  list: any = [];
  //传入详情的值
  inValueCode: string;
  totalNum: string;
  //模态框
  display: boolean = false;
  //1 更新 2添加 3详情
  showModel: string;
  //弹出框的头信息
  headerTitle: string;
  //提示信息
  msgs: Message[] = [];
  isRcord: any = [];
  isUnit: any = [];
  IsOrigin: any = [];
  IsLable2: any = [];
  list1: any = [];
  list2: any = [];
  list3: any = [];
  list4: any = [];
  arrList: any = [];
  partyIdList: any = [];
  total: number;
  effDt: Date;
  invalDt: Date;
  invalDtStart: Date;
  invalDtEnd: Date;
  treeOrgName: any;
  institArea: string;
  //获取所有码值
  code: any;
  first: number = 0;//分页控制
  tabMesg: any;//表格中无数据时显示的内容
  constructor(
    public httpService: ValuationHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc,
    public commonHttpService: CommonHttpService,
    public router: Router) {
    this.codeValues();
    this.IsOrigin = this.code['IsOrigin'];
    this.IsLable2 = this.code['IsLable2'];
    this.isRcord = this.code['IsRcord'];
    this.isUnit = this.code['IsUnit'];
  }

  ngOnInit() {
    let session = window.sessionStorage;
    if (session.getItem('para')) {
      session.removeItem('para');
    }
    this.beanInterest.pageSize = 10;
    this.beanInterest.pageNum = 1;

    // 利率种类名称
    let param = { intRateName: "" }
    this.httpService.queryRateTypeInternalListService(param).subscribe(data => {
      if (data.returnCode.code == "success") {
        if (data.resultList) {
          this.arrList = this.commfunc.toChangeTree(data.resultList, "intRateName", "intRateCgy")
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
    });
    //贷款金额
    let param1 = { dmsnlChiNm: "贷款金额" };
    this.httpService.querydetailTmsDicService(param1).subscribe(data => {
      if (data.resultList) {
        this.list1 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue");
        this.list1.unshift({ label: '请选择', value: '' })
      }
    });
    //担保方式
    let param2 = { dmsnlChiNm: "担保方式" };
    this.httpService.querydetailTmsDicService(param2).subscribe(data => {
      if (data.resultList) {
        this.list2 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue");
        this.list2.unshift({ label: '请选择', value: '' })
      }
    });
    //期限
    let param3 = { dmsnlChiNm: "期限" };
    this.httpService.querydetailTmsDicService(param3).subscribe(data => {
      if (data.resultList) {
        this.list3 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue");
        this.list3.unshift({ label: '请选择', value: '' })
      }
    });
    //积数比
    let param4 = { dmsnlChiNm: "积数比" };
    this.httpService.querydetailTmsDicService(param4).subscribe(data => {
      if (data.resultList) {
        this.list4 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue");
        this.list4.unshift({ label: '请选择', value: '' })
      }
    });
    let params = {
      orgId: this.institArea,
      relationshipType: "0"
    }
    this.commonHttpService.queryOrgByOrgIdAndRelationship(params).subscribe(data => {
      this.treeOrgName = this.commfunc.toChangeTree(data.orgList, "institArea", "orgId")
      // if (this.treeOrgName.length > 0) {
      //   this.orgName = this.treeOrgName[0].label;
      //   this.orgId = this.treeOrgName[0].value;
      // }

    })
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  querySecle() {
    this.beanInterest.pageSize = 10;
    this.beanInterest.pageNum = 1;
    this.first = 0;
    this.toquery();
  }
  toquery() {
    let aa = this.commfunc.transDateN(this.effDt);//生效日期
    let bb = this.commfunc.transDateN(this.invalDt);
    this.beanInterest.effDt = aa;
    this.beanInterest.invalDt = bb;
    this.beanInterest.invalDtStart = this.commfunc.transDateN(this.invalDtStart);
    this.beanInterest.invalDtEnd = this.commfunc.transDateN(this.invalDtEnd);
    this.beanInterest.institArea = this.orgId;
    this.httpService.queryLoansBaseRateInternalService(this.beanInterest).subscribe(data => {
      if (data.resultList == "" || data.resultList == null) {
        this.tabMesg = tableMessage
      }
      if (data.resultList) {
        this.list = data.resultList;

      }
      let j = 0;
      for (let i in this.list) {
        if (this.list[i]['institArea']) {
          this.partyIdList[j] = this.list[i]['institArea'];
          j++;
        }
      }
      let params = {
        partyIdList: this.partyIdList
        // relationshipType: "0"
      }
      this.commonHttpService.queryPartyGroupListByPartyId(params).subscribe(data => {

        this.treeOrgName = this.commfunc.toChangeTree(data.partyGroupList, "groupName", "partyId")
        // this.orgName=this.treeOrgName;
      })
      this.total = data.total;

      // 错误条件查询的提示
      if (data.returnCode.code != "success") {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }

    }, err => {
      this.tabMesg = tableMessage
    })
  }
  //机构数服务

  //公共机构树点击事件
  chooseTree() {
    this.headerTitle = '选择机构树';
    this.showModel = '2';
    this.display = true;
  }

  //公共机构树回掉
  treeCall(param) {
    this.display = false;
    this.treeOrgName = param.orgId;
    let params = {
      orgId: param.orgId,
      relationshipType: "0"
    }
    this.commonHttpService.queryOrgByOrgIdAndRelationship(params).subscribe(data => {
      this.treeOrgName = this.commfunc.toChangeTree(data.orgList, "orgName", "orgId")
      this.orgName = this.treeOrgName[0].label;
      this.orgId = this.treeOrgName[0].value;
    })
  }
  //重置
  newSet() {
    this.beanInterest = new BeanInterest();
    this.effDt = null;
    this.invalDt = null;
    this.invalDtStart = null;
    this.invalDtEnd = null;
    this.beanInterest.pageSize = 10;
    this.beanInterest.pageNum = 1;
    this.orgName = null;
    this.orgId = null;
    // this.first = 0;
    // this.toquery();
  }
  //清空机构树
  clean() {
    this.orgName = null;
    this.orgId = null;
  }
  //新增的回调函数
  addCall(param) {
    this.display = param;
  }
  //修改的回掉函数
  updateCall(param) {
    this.display = false;
    if (param != false) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param });
    }
    this.toquery()
  }
  //修改
  update(param) {
    this.headerTitle = '贷款基础利率维护-修改';
    this.showModel = '1';
    this.display = true;
    this.inValueCode = param;
    setTimeout(() => {
      this.inValueCode = "";
    }, 2000)
  }
  //查询子页面的回调函数
  queryCall(param) {
    this.display = param;
  }
  //导入
  import() {
    this.headerTitle = '贷款基础利率维护导入';
    // this.showModel = '4';
    // this.display = true;
    this.router.navigate(['/pages/tzb/valuation/base-interest/base-interest-import'])
  }
  //新增
  add(param) {
    this.headerTitle = '贷款基础利率维护新增';
    // this.display = true;
    // this.showModel = '2';
    this.router.navigate(['/pages/tzb/valuation/base-interest/base-interest-add'])
  }
  detail(param) {
    this.headerTitle = '贷款基础利率维护-详情';
    this.display = true;
    this.showModel = '3';
    this.inValueCode = param;
    setTimeout(() => {
      this.inValueCode = "";
    }, 2000)
  }

  delete(car) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = {
          intRateCode: car.intRateCode,
          intRateCgy: car.intRateCgy,
          effDt: car.effDt,
        };
        this.httpService.deleteLoansBaseRateService(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.first = 0;
            this.toquery();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.beanInterest.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.beanInterest.pageNum = currentPage;
    this.first = (currentPage - 1) * this.beanInterest.pageSize;
    //调用查询的方法
    this.toquery();
    //  this.beanInterest.pageNum = 1;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}
